.product-container {
    flex: 1;
    margin: 5px;
    min-width: 280px;
    height: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f5fbfd;
    position: relative;

    max-width: 400px;
    padding: 10px 30px;
}

.product-img {
    height: 75%;
    z-index: 2;

    width: 100%;
    object-fit: cover;
}

.product-info {
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease;
    cursor: pointer;
}
.product-info:hover {
    opacity: 1;
}

.product-icon {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    transition: all 0.5s ease;
}

.product-icon:hover {
    background-color: #e9f5f5;
    transform: scale(1.1);
}

/* .product-container:hover {

  } */

.product-user {
    position: absolute;
    height: 32px;
    display: block;
    bottom: 2%;
}
.product-category {
    position: absolute;
    height: 32px;
    display: block;
    text-transform: capitalize;
    top: 2%;
}
