.sliderContainer {
    width: 100%;
    height: 100vh;
    display: flex;
    position: relative;
    overflow: hidden;
}

.button-left,
.button-right {
    width: 50px;
    height: 50px;
    background-color: #706969;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    cursor: pointer;
    opacity: 0.5;
    z-index: 2;
    font-weight: 300;
}

.button-left {
    left: 10px;
}
.button-right {
    right: 10px;
}
.div-slider-wrapper {
    height: 100%;
    display: flex;
    transition: all 1.5s ease;
    display: flex;
}

.div-slide {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
}

.div-container-img {
    height: 100%;
    /* flex: 2; */
}
.div-container-img > img {
    width: 100%;
    height: 80%;
}

.div-container-info {
    /* flex: 1; */
    padding: 50px;
    display: flex;
    flex-direction: column;
}

.div-container-info > h1 {
    margin: 20px 0;
    font-size: 70px;
}
.div-container-info > p {
    font-size: 27px;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 3px;
}
.div-container-info > button {
    font-size: 20px;
    background-color: transparent;
    padding: 10px;
    cursor: pointer;
}

.div-container-info > button:hover {
    background: rgb(13, 223, 58);
    color: rgb(0, 0, 0);
    font-weight: bold;
}


@media all and (max-width: 1024px) {
    .div-slide {
        flex-direction: column;
    }

    .div-container-img {
        height: 60%;
    }
    .div-container-info {
        padding: 0;
    }
    .div-container-info > h1 {
        font-size: 50px;
        margin-bottom: 0px;
    }

    .div-container-img > img {
        width: 100%;
        height: 100%;
    }
}


